import Bmob from "hydrogen-js-sdk";
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

let that;
let search_text;
let uid;
export default {
	components: {
		uniNavBar,
	},
	data() {
		return {
			people: [], //获得人员数组
			is_producer: false,
			is_custom: false,
			showOrder: false, //是否显示排序
			showOptions:false,
			type: '', //选择类型,
			customHeader: {
				num: 0,
				debtMoney: 0
			},

			headerSelection: {
				order: {
					"order": "-createdAt"
				},
				customCategory:{},
				options: '',
			},
			orders: [{
				"desc": "应收欠款",
				"notice": "从高到低",
				"order": "-debt",
				"checked": false,
			}, {
				"desc": "应收欠款",
				"notice": "从低到高",
				"order": "debt",
				"checked": false,
			}, {
				"desc": "创建日期",
				"notice": "最新",
				"order": "-createdAt",
				"checked": true,
			}, {
				"desc": "创建日期",
				"notice": "最晚",
				"order": "createdAt",
				"checked": false,
			}, {
				"desc": "名字",
				"notice": "正序",
				"order": "goodsName",
				"checked": false,
			}]
		}
	},
	onLoad(options) {
		that = this;
		uid = uni.getStorageSync('uid');

		if (options.type == "custom" || options.type == "customFinance") {
			that.is_custom = true;
			that.load_data("customs")
		}

		that.type = options.type
	},

	onShow() {
		uni.removeStorageSync("customs")
		uni.removeStorageSync("custom_type")
		
		if(uni.getStorageSync("isClickShaiXuan")){ //判断是否下级页面进行了操作
			that.headerSelection.customCategory = uni.getStorageSync("customCategory") || ''
			that.load_data("customs")
		}else{
			that.load_data("customs")
		}
		
		
	},
	onUnload() {
		search_text = ""
		uni.removeStorageSync("isClickShaiXuan")
	},
	methods: {

		//头部的options选择
		selectd(type) {
			if (type == "order") {
				that.showOrder = true
			}else if (type == "customCategory") {
				uni.navigateTo({
					url: "/pages/manage/customClass/customClass?type=choose"
				})
			} else if (type == "options") {
				that.showOptions = true
			}
			uni.setStorageSync("isClickShaiXuan",true);
		},
		
		//选择当前排序
		selectOrder(item){
			for(let item of that.orders){
				item.checked = false
			}
			item.checked = true
			that.headerSelection.order = item
			that.load_data("customs")
		},

		//打电话
		makePhoneCall(phone) {
			if (phone) {
				uni.makePhoneCall({
					phoneNumber: phone //仅为示例
				});
			} else {
				uni.showToast({
					icon: "none",
					title: "未填写联系电话"
				})
			}
		},
		
		//点击客户去到详情
		gotoDetail(item) {
			if (that.is_custom) {
				return
			}
			uni.navigateTo({
				url: 'custom_detail/custom_detail?id='+item.objectId
			})
		},

		//选择此供货商
		select_this(e) {
			let custom =JSON.parse(e.detail.value);
			uni.setStorageSync("custom", custom)
			uni.navigateBack({
				delta: 1
			})
		},

		//前去添加客户
		goto_add() {
			uni.navigateTo({
				url: "add/add?type=customs"
			})
		},

		//输入内容筛选
		input_confirm(e) {
			search_text = e.detail.value
			that.load_data("customs")
		},
		
		//modal重置的确认点击
		option_reset() {
			that.headerSelection = {
				order: {"order":"-createdAt"},
				customCategory:{},
				options: '',
			};
			that.orders =  [{
				"desc": "应收欠款",
				"notice": "从高到低",
				"order": "-debt",
				"checked": false,
			}, {
				"desc": "应收欠款",
				"notice": "从低到高",
				"order": "debt",
				"checked": false,
			}, {
				"desc": "创建日期",
				"notice": "最新",
				"order": "-createdAt",
				"checked": true,
			}, {
				"desc": "创建日期",
				"notice": "最晚",
				"order": "createdAt",
				"checked": false,
			}, {
				"desc": "名字",
				"notice": "正序",
				"order": "goodsName",
				"checked": false,
			}]
			that.load_data("customs")
		},

		//加载数据
		load_data(type) {
			uni.showLoading({title:"加载中..."});
			that.customHeader.num = 0;
			that.customHeader.debtMoney = 0;
			
			const query = Bmob.Query(type);
			query.equalTo("parent", "==", uid);
			query.order(that.headerSelection.order.order);
			query.limit(500);
			if (search_text) {
				query.equalTo("custom_name", "==", {
					"$regex": "" + search_text + ".*"
				});
			}
			if (that.headerSelection.customCategory.type == 1) {
				query.equalTo("customFristClass", "==", that.headerSelection.customCategory.objectId);
			} else {
				query.equalTo("customSecondClass", "==", that.headerSelection.customCategory.objectId);
			}
			query.include("customFristClass","customSecondClass")
			query.find().then(res => {
				console.log(res)
				let thisCustom = res
				uni.hideLoading();
				
				const query = Bmob.Query("order_opreations");
				query.equalTo("type", "==", -1);
				query.equalTo("extra_type", "==", 1);
				query.equalTo("master", "==", uid);
				query.include("opreater", "account", "custom", "producer", "secondClass", "fristClass");
				query.statTo("sum", "debt");
				query.statTo("groupby", "custom");
				query.find().then(res => {
					 let debtCustom = res
					 for(let item1 of thisCustom){
						 for(let item2 of debtCustom){
							 if(item2.custom && item2.custom.objectId == item1.objectId){
								 item1.debt = item2._sumDebt
							 }
						 }
					 } 
					 
					 for (let item of thisCustom) {
						 item.debt = item.debt?item.debt:0
						 if(item.orginDebt){
							 item.debt = item.debt + item.orginDebt
						 }else{
							 item.debt = item.debt + 0
						 }
					 	that.customHeader.num += 1;
					 	that.customHeader.debtMoney += item.debt || 0
					 }
					 
					 that.people = thisCustom;
				})
			});
		},

	}
}
